const initialState = {
    movies: [],
    msg: "Welcome!",
};
 
const moviesReducer = (state = initialState, action) => {
    
    let newState = {};
    switch (action.type) {

        case 'START_SEARCHING': 
            newState = Object.assign({}, state, {
                msg: "start searching..."
            });
            return newState;

        case 'MOVIES_FOUND':
            newState = Object.assign({}, state, {
                movies: action.data,
            });
            return newState;

        case 'FINISH_DOWNLOADING':
            newState = Object.assign({}, state, {
                msg: "Movies found"
            });
            return newState;

        case "TITLE_CHANGE":
            newState = Object.assign({}, state,{
               msg:"title changed" 
            });
            newState.date = Date.now();
            return newState;


        default:
            return state;
    }
};
export default moviesReducer;