export const SearchMovie = searchedMovie => {
   
    return async function (dispatch) {
       
        dispatch({
            type: "START_SEARCHING",
            data: null
        });

        
        let res = await fetch(`//www.omdbapi.com/?s=${searchedMovie}&apikey=fe9450d0`);
        let data = await res.json();

        dispatch({
            type: "MOVIES_FOUND",
            data: data.Search
        });

        dispatch({
            type: "FINISH_DOWNLOADING",
            data: null
        });

        

    };
}

export const ChangeTitle = movieName => {

    return async function (dispatch) {

        let newNamesArr = [];
        dispatch({
            type: "TITLE_CHANGE",
            data: movieName
        });
        newNamesArr.push(movieName);

    }

}