import React, { Component } from 'react';
import { connect } from "react-redux";
import { ChangeTitle } from "../state/actions";


class Movie extends Component { 



state={
  movieName:""
}

componentDidMount()
{
    this.setState({ movieName: this.props.m.Title });
} 
 


  render() {
    return (
      <div className="Movie">
      <input className="movieTitle" placeholder="Enter movie name" name="movieName" type="text" value={this.state.movieName} onChange={this.handleChangeTitle.bind(this)} onBlur={this.ChangeTitle.bind(this)}  /><br/>
      <img src={this.props.m.Poster} alt=""/>
      </div>
    );
  }

  handleChangeTitle(ev) { 
    this.setState({ [ev.target.name]: ev.target.value });
  }

  ChangeTitle(){
    this.props.ChangeTitle(this.state.movieName)
  }

}

const mapDispatchToProps = dispatch => {  
  return  { 
    
      ChangeTitle: function(movieName) { 
           
           return  dispatch(ChangeTitle(movieName));
          
        }
      }
  };
  

const movie = connect(null, mapDispatchToProps)(Movie);

export default movie;
