import React, { Component } from 'react';
import { connect } from "react-redux";
import { SearchMovie } from "../state/actions";
import { InputGroup, InputGroupAddon, Button, Input } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Movie from './Movie';

class AllMovies extends Component {


  render() {
    return (
      <div>

        <div className="alert alert-info" role="alert">
          {this.props.msg}
        </div>

        <div className="header">
          <h1>OMDb API</h1>
          <h3>Search for your movie:</h3>

          <InputGroup>
            <Input type="text" name="searchedMovie" onChange={this.handleChange.bind(this)} />
            <InputGroupAddon addonType="append">
              <Button color="info" type="button" onClick={this.SearchMovie.bind(this)}><FontAwesomeIcon icon="search" /></Button><br />
            </InputGroupAddon>
          </InputGroup>

        </div>

        <div className="allMovies">
          {this.props.movies.map(m => <Movie key={m.imdbID} m={m} />)}
        </div>

      </div>
    );
  }

  handleChange(ev) {
    this.setState({ [ev.target.name]: ev.target.value });
  }

  SearchMovie() {
    this.props.SearchMovie(this.state.searchedMovie)
  }

}

const mapStateToProps = state => {
  return { movies: state.movies, msg: state.msg };
};

const mapDispatchToProps = dispatch => {
  return {

    SearchMovie: function (searchWord) {

      return dispatch(SearchMovie(searchWord));

    }
  }
};


const allmovies = connect(mapStateToProps, mapDispatchToProps)(AllMovies);

export default allmovies;
